nav{
    height: 5rem;
    width: 100vw;
    background-color: var(--accent-color);
    display: grid;
    place-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
}
/* only shows on medioum and small screens */
.nav__toggle-btn{
    display: none;
}
.nav__container{
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}
.logo{
    width: 10rem;
    display: block;
}
.nav__links{
    display: flex;
    gap: 2.6rem;
}
.nav__links a:hover{
    opacity: 0.7;
    transition: var(--transition);
}

.active-nav{
    position: relative;
}
.active-nav::after{
    content: '';
    display: block;
    width: 1.3rem;
    height: 1.3rem;
    position: absolute;
    background: var(--accent-color);
    left: calc(50% - 0.65rem);
    transform: rotate(45deg);
    margin-top: 0.9rem;
}

/* media queries for medium and small screens */
@media screen and (max-width:1024px) {
    .nav__toggle-btn{
        display: inline-block;
        background-color: transparent;
        font-size: 1.6rem;
        cursor: pointer;
        color: white;
    }
    .nav__links{
        position: absolute;
        top: 100%;
        right: 0;
        flex-direction: column;
        gap:0;
        perspective: 400px;
    }
    .active-nav,.active-nav::after{
        display: none;
    }
    .nav__links li{
        height: 4rem;
        width: 100%;
        box-shadow: -2rem 2rem 5rem rgba(0, 0, 0, 0.4);
        animation: navAnimation 600ms ease forwards;
        transform: rotateX(90deg);
        opacity: 0;
        transform-origin: top;
    }
    .nav__links li:nth-child(2){
        animation-delay: 200ms;
    }
    .nav__links li:nth-child(3){
        animation-delay: 400ms;
    }
    .nav__links li:nth-child(4){
        animation-delay: 600ms;
    }
    .nav__links li:nth-child(5){
        animation-delay: 800ms;
    }
    .nav__links li:nth-child(6){
        animation-delay: 1s;
    }
    .nav__links li:nth-child(7){
        animation-delay: 1.2s;
    }
    @keyframes navAnimation {
        to{
            transform: rotateX(0);
            opacity: 1;
        }
    }
    .nav__links li a{
        background-color: var(--accent-color);
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        padding: 1rem 5rem 1rem 3rem;
    }

    .show__nav{
        display: flex;
    }
    .hide__nav{
        display: none;
    }
}



