.contact__container{
    display: grid;
    place-items: center;
}

.contact__wrapper{
    width: fit-content;
    display: flex;
    gap: 1.5rem;
}

.contact__wrapper a{
    display: inherit;
    background: #262626;
    font-size: 1.6rem;
    border: 1px solid transparent;
    transition: var(--transition);
    padding: 1.6rem;
    border-radius: 1.5rem;
}

.contact__wrapper a:hover{
    border-color: #777;
    background: transparent;
}


/* MEDIA QUERIES ( small screens ) */

@media screen and (max-width:600px) {
    .contact__wrapper{
        gap: 1.3rem;
    }
    .contact__wrapper a{
        padding: 1rem;
        font-size: 1.1rem;
        border-radius: 1rem;
    }
}
