.main__header{
    width: 100vw;
    height: 100%;
    display: grid;
    place-items: center;
    margin-top: 10rem;
}
.main__header-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    justify-content: space-between;
    gap: 5rem;
}
.main__header-left h4{
    color: #777;
    margin-bottom: 1rem;
    font-weight: 400;
}
.main__header-left p{
    margin: 1rem 0 3.5rem 0;
    font-size: 1.1rem;
}


.main__header-right{
    display: grid;
    place-items: center;
    position: relative;
}



/* =============  PROGRAMS  =============== */

.programs{

}
.programs__wrapper{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 3rem;
    margin-top: 4rem;
}
.programs__program a{
    display: flex;
    align-items: center;
    margin-top: 2.6rem;
}
.programs__program:hover a{
    background-color: #222;
}


/* =============  VALUES  =============== */

.values__container{
    display: grid;
    grid-template-columns: 38% 50%;
    gap: 12%;
}

.values__image{
    filter: saturate(0.25);
    transition: var(--transition);
}

.values__image:hover{
    filter: saturate(1);
}


.values__right>p{
    margin: 1.5rem 0 5rem;
}

.values__wrapper{
    display: grid;grid-template-columns: 1fr 1fr;
    gap: 3.4rem 2.4rem;
}
.card.values__value{
    padding-top: 3.5rem;
    text-align: left;
    position: relative;
}
.card.values__value span{
    position: absolute;
    top: -1.5rem;
}


/* =============  FAQs  =============== */

.faqs__wrapper{
    margin-top: 4rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem 5rem;
    align-items: start;
}

.faq{
    background-color: #222;
    padding: 2rem;
    border-radius: 1rem;
}

.faq div{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.faq div button{
    cursor: pointer;
}
.faq__icon{
    background: transparent;
    color: #ccc;
    font-size: 1.5rem;
}

.faq p{
    margin-top: 1.5rem;
}




/* =============  TESTIMONIALS =============== */

.testimonials{
    width: 50rem;
    margin-inline: auto;
}

.testimonials__head{
    justify-content: center;
}
.card.testimonial{
    position: relative;
    text-align:left;
    margin-top: 5rem;
}

.testimonial__avatar{
    width: 4rem;
    height: 4rem;
    border-radius: 1.5rem;
    overflow: hidden;
    position: absolute;
    border: 2px solid transparent;
    top: -2rem;
    left: calc(50%-2rem);
    box-shadow: 0 1.5rem 2rem rgba(26, 26, 55, 0.4);
    transition: var(--transition);
}
.testimonial:hover .testimonial__avatar{
    border-radius: 50%;
    border: 2px solid #444;
}
.testimonial__quote{
    font-style: italic;
    margin: 1rem 0 2rem;
}
.card small.testimonial__title{
    margin-top: 0.3rem;
}

.testimonials__btn-container{
    width: fit-content;
    margin: 2.5rem auto 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

.testimonials__btn{
    background: transparent;
    cursor: pointer;
}
.testimonials__btn svg{
    fill: #777;
    font-size: 1.8rem;
    width: 3rem;
    height: 3rem;
}



/* ============= FOOTER =============== */

footer{
    background-color: #111;
    margin-top: 7rem;
    padding-top: 7rem;
    font-size: 0.9rem;
    color: #777;
}

.footer__container{
    display: grid;
    grid-template-columns: 26rem 1fr 1fr 1fr;
    gap: 6rem;
}

.footer__container article{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.7rem;
}

.footer__container article p{
    margin-top: 0.5rem;
}
.footer__container article h4{
    margin-bottom: 0.6rem;
    font-size: 1rem;
}
.footer__socials{
    margin-top: 1.5rem;
    display: flex;
    gap: 1rem;
    align-items: center;
}
.footer__socials a{
    display: inherit;
    background: #444;
    padding: 0.7rem;
    border: 1px solid transparent;
    border-radius: 0.6rem;
    transition: var(--transition);
}
.footer__socials a:hover{
    border-color: #777;
    background: transparent;
}

.footer__copyright{
    color: #777;
    text-align: center;
    padding: 1.5rem;
    border-top: 1px solid #444;
    margin-top: 5rem;
}



/* MEDIA QUERIES ( medium screens ) */

@media screen and (max-width:1024px) {
    /* MAIN HEADER */
    .main__header{
        height: fit-content;
        padding: 2rem 0;
    }
    .main__header-container{
        gap: 0;
    }

    /* PROGRAMS */
    .programs__wrapper{
        grid-template-columns: 1fr 1fr;
    }
    /* VALUES */
    .values__container{
        grid-template-columns: 1fr;
        justify-content: center;
        gap: 4rem;
    }
    .values__wrapper{
        gap: 4rem 3rem;
    }
    /* .values__image{
        display: none;
    } */

    /* FAQs */
    .faqs__wrapper{
        grid-template-columns: 1fr;
    }

     /* TESTIMONIALS */
     .testimonials{
        width: 80%;
     }

      /* FOOTER */
      .footer__container{
        grid-template-columns: 1fr 1fr;
        gap: 4rem;
      }
}


/* MEDIA QUERIES ( small screens ) */

@media screen and (max-width:600px) {
     /* MAIN HEADER */
     .main__header{
        margin-top: 0;
        padding: 10rem 0 0;
        height: 100vh;
     }
     .main__header-container{
        grid-template-columns: 1fr;
        gap: 4rem;
     }
    /* PROGRAMS */
    .programs__wrapper{
        grid-template-columns: 1fr;
        gap: 2rem;
        /* padding: 1rem; */
    }
    .programs__program{
        width: 85%;
        margin: 0 auto;
    }

     /* VALUES */
     .values__container{

    }
    .values__wrapper{
        grid-template-columns: 1fr;
        gap: 3rem;
    }
    .card.values__value{
        width: 85%;
        margin: 0 auto;
    }

    /* FAQs */
    .faqs__wrapper{
        gap: 1rem;
        margin-top: 3rem;
    }
    .faq{
        padding: 1.6rem;
    }
     /* TESTIMONIALS */
     .testimonials{
        width: 100%;
     }
      /* FOOTER */
      footer{
         margin-top: 2rem;
      }
      .footer__container{
        grid-template-columns: 1fr;
        gap: 3rem;
      }
}