.about__section-image{
    /* border-radius: 50%; */
    overflow: hidden;
    transform: skewX(15deg);
    transition: var(--transition);
}
.about__section-image:nth-child(2){
    transform: skewX(15deg);
}
.about__section-image:hover{
    transform: skewX(0);
}
.about__section-content h2{
    margin-bottom: 2rem;
}
.about__section-content p{
    margin-bottom: 1rem;
}



.about__story-container,.about__mision-container{
    display: grid;
    grid-template-columns: 30% 50%;
    gap: 15%;
}
.about__vision-container{
    display: grid;
    grid-template-columns: 50% 30%;
    gap: 15%;
}


/* MEDIA QUERIES ( medium screens ) */

@media screen and (max-width:1024px) {
    .about__section-image{
        width: 60%;
    }
    .about__section-content h2{
        margin-bottom: 1.2rem;
    }
    .about__story-container,.about__mision-container,.about__vision-container{
        grid-template-columns: 1fr;
        gap: 3rem;
    }
    .about__vision .about__section-image{
        grid-row: 1;
    }
}

/* MEDIA QUERIES ( small screens ) */

@media screen and (max-width:600px) {
    .about__section-image{
        width: 80%;
        margin-inline: auto;
    }
    .about__story-container,.about__mision-container,.about__vision-container{
        gap: 2rem;
    }
}