*, *::before, *::after{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
}

:root{
    --text-color: #ccc;
    --back-color: #161616;
    /* --accent-color: rgb(71, 127, 71); */
    --accent-color: #161616;
    --container-width-lg: 80%;
    --container-width-md: 90%;
    --transition: 0.5s;
}


/* Generel Styles */

body{
    font-family: 'Montserrat', sans-serif;
    color: var(--text-color);
    line-height: 1.7;
    overflow-x: hidden;
    background: #000 url('./images/bg_texture.png');
}
.container{
    width: var(--container-width-lg);
    max-width: 1920px;
    margin: auto;
}
h1,h2,h3,h4{
    line-height: 1.3;
    /* color: var(--accent-color); */
}
h1{
    font-size: 3rem;
}
h2{
    font-size: 2rem;
}
a{
    color: #eee;
}

img{
    display: block;
    object-fit: cover;
    width: 100%;
}

.btn{
    width: fit-content;
    color: #eee;
    margin: 0 auto;
    padding: 0.9rem 2rem;
    background-color: var(--accent-color);
    border-radius: 1.5rem;
    transition: var(--transition);
}
.btn:hover{
    opacity: 0.7;
}
.btn.lg{
    padding: 1.1rem 3rem;
    border-radius: 2rem;
    font-size: 1.1rem;
}
.btn.sm{
    padding: 0.4rem 1.2rem;
    font-size: 0.9rem;
}




section{
    padding: 8rem 0;
}

.section__head{
    display: flex;
    align-items: center;
    gap: 1rem;
}

.section__head span{
    display: inherit;
    background-color: var(--accent-color);
    padding: 0.8rem;
    border-radius: 1rem;
    color: #eee;
    font-size: 1.5rem;
}


.card{
    background-color: #111;
    border: 2px solid transparent;
    text-align: center;
    padding: 3rem 2rem;
    border-radius: 0 3rem 0 3rem;
    transition: var(--transition);
}
.card:hover{
    background: transparent;
    border-color: #333;
}

.card span{
    width: 3rem;
    height: 3rem;
    background: var(--accent-color);
    color: #eee;
    font-size: 1.3rem;
    padding: 0.8rem;
    display: grid;
    place-items: center;
    margin-bottom: 1.3rem;
    margin-inline: auto;
    border-radius: 1rem;
    transition: var(--transition);
}
.card:hover span{
    background-color: #222;
}

.card small{
    margin-top: 1rem;
    display: block;
    font-weight: 300;
}



.header{
    margin-top: 5rem;
    height: 20rem;
    overflow: hidden;
    border-bottom: 1px solid #777;
}

.header__container{
    width: 100%;
    height: 100%;
    position: relative;
    display: grid;
    place-items: center;
    background: black;
}

.header__container-bg{
    position: absolute;
    width: 100%;
}
/* .header__container-bg img{
    opacity: 0.5;
} */
.header__content{
    position: relative;
    width: 44%;
    margin: 0 auto;
    text-align: center;
    color: #ccc;
}

.header__content h2{
    margin-bottom: 1rem;
}

.header__content p{
    color: rgba(255, 255, 255, 0.6);
}




/* MEDIA QUERIES ( medium screens ) */

@media screen and (max-width:1024px) {
    .container{
        width: var(--container-width-md);
    }
    h1{
        font-size: 2rem;
    }
    h2{
        font-size: 1.6rem;
    }
    section{
        padding: 5rem 0;
    }
    .header__content{
        width: 76%;
    }
}


/* MEDIA QUERIES ( small screens ) */

@media screen and (max-width:600px) {
    h1{
        font-size: 2.2rem;
        line-height: 1.3;
    }
    .header{
        height: fit-content;
    }
    /* section{
        margin-top: 7rem;
    } */
    .header__content{
        width: var(--container-width-md);
        padding: 3rem 0;
    }
    .header__content p{
        font-size: 0.85rem;
    }
}